		created () {
			let idx = 0
	  		this.formData.colCompany.forEach((item)=>{
	  			let rowShow = []
	  			for(let key in item){
	  				rowShow.push(true)
	  			}
	  			this.$set(item,'index',idx++)
	  			this.tableInputShow.push(rowShow)
	  		})
		},
		mounted () {

		},
		methods: {
			handleTabClick(idx) {
				this.activeIdx = idx
			},
			exportModel () {

			},
			handleAdd () {

			},
			handleDelete () {

			},
			// 点击文字出现input/select并焦点
	  		handleInputShow (idx,col) {
	  			let inputEle = 'table-input' + idx + col
	  			this.$set(this.tableInputShow[idx],col,false)
	  			this.$nextTick(()=>{
	  				this.$refs[inputEle] && this.$refs[inputEle].focus()
	  			})
	  		},
	  		handleSelectionChange (val) {
	  			 this.multipleSelection = val
	  		},
	  		handleBlur (idx,col,key) {
	  			this.$set(this.tableInputShow[idx],col,true)
	  			// this.$set(this.tableData[idx],'key')
	  		},
	  		handleAddYear () {
	  			let num = this.yearOptions[this.yearOptions.length - 1]
	  			num++
	  			this.yearOptions.push(num)
	  			this.$nextTick(()=>{
	  				this.handleYearClick(num)
	  			})
	  		},
	  		handleYearClick (year) {
	  			this.activeYear = year
	  		},
	  		handleLeftClick1 () {
	  			this.yearOptions.splice(this.yearOptions.findIndex(item => item === this.activeYear), 1)
	  			this.activeYear = this.yearOptions[this.yearOptions.length - 1]
	  			this.dialogShow1 = false
	  		},
	  		// 数据改变之后保存索引值到数组中
	  		handleValueChange (rowIdx) {
	  			// this.savedFlag = false
	  			// let isInArray = this.unsavedData.findIndex((o)=>{
	  			// 	return o === rowIdx
	  			// })

	  			// if (isInArray === -1) {
	  			// 	this.unsavedData.push(rowIdx)
	  			// }
	  		},
		},
		computed:{
			totalOutlay () {
				let num = 0
				this.formData.outlayTotal.forEach((o)=>{
					if (Number(o.num)) {
						num = num + Number(o.num)
					}
				})
				return num
			}
		}
	}
